.home {
  font-family: 'Georgia', serif;
}

.hero {
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  position: relative;
  overflow: hidden;
}

.hero-background {
  position: absolute;
  top: -10%;
  left: -10%;
  width: 120%;
  height: 120%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  will-change: transform;
  transition: transform 0.1s ease-out;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
  will-change: opacity;
  transition: opacity 0.1s ease-out;
}

.hero-content {
  max-width: 800px;
  padding: 0 20px;
  z-index: 2;
  position: relative;
  will-change: transform;
}

.glass-container {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 3rem 2.5rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
  transition: all 0.3s ease;
}

.hero-title,
.hero-subtitle,
.cta-button {
  transition: transform 0.1s ease-out, opacity 0.1s ease-out;
}

.hero-title {
  font-size: 3.5rem;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  color: #f5f1eb;
}

.hero-subtitle {
  font-size: 1.5rem;
  margin-bottom: 2rem;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  color: #e8e2d9;
}

.cta-button {
  display: inline-block;
  background: linear-gradient(135deg, var(--primary-color), var(--primary-dark));
  color: #ffffff;
  padding: 15px 40px;
  border-radius: 30px;
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: bold;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  border: 2px solid transparent;
  cursor: pointer;
  font-family: inherit;
}

.cta-button:hover {
  background: linear-gradient(135deg, var(--primary-dark), var(--primary-color));
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
  color: #ffffff;
  border-color: var(--primary-dark);
}

.about {
  padding: 80px 0;
  background-color: var(--background-color);
  position: relative;
}

.about .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.about h2 {
  text-align: center;
  color: var(--primary-color);
  font-size: 2.5rem;
  margin-bottom: 40px;
  position: relative;
}

.about h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 3px;
  background: linear-gradient(90deg, var(--primary-color), var(--primary-light));
}

.about-content {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

.about-content p {
  font-size: 1.2rem;
  line-height: 1.8;
  color: var(--text-color);
  margin-bottom: 25px;
  text-align: justify;
}

.gallery {
  padding: 80px 0;
  background: var(--background-color);
  position: relative;
}

.gallery .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.gallery h2 {
  text-align: center;
  color: var(--primary-color);
  font-size: 2.5rem;
  margin-bottom: 50px;
  position: relative;
}

.gallery h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 3px;
  background: linear-gradient(90deg, var(--primary-light), var(--primary-color));
}

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
}

.gallery-item {
  position: relative;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  transition: all 0.3s ease;
  background: white;
  display: flex;
  flex-direction: column;
  min-height: 300px;
}

.gallery-item:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.25);
}

.gallery-image {
  width: 100%;
  height: 300px;
  min-height: 300px;
  object-fit: cover;
  object-position: center;
  display: block;
  transition: transform 0.5s ease;
  background: #f5f5f5;
}

.gallery-item:hover .gallery-image {
  transform: scale(1.05);
}

.gallery-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(to top, rgba(139, 115, 85, 0.95), rgba(139, 115, 85, 0.7), transparent);
  color: white;
  padding: 20px;
  text-align: center;
  font-size: 1.2rem;
  font-weight: bold;
  z-index: 1;
  word-wrap: break-word;
  overflow-wrap: break-word;
}

@media (max-width: 768px) {
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.2rem;
  }
  
  .cta-button {
    padding: 12px 30px;
    font-size: 1.1rem;
  }
  
  .about, .gallery {
    padding: 50px 0;
  }
  
  .about h2, .gallery h2 {
    font-size: 2rem;
  }
  
  .about-content p {
    font-size: 1.1rem;
    text-align: center;
  }
  
  .gallery-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .gallery-image {
    height: 250px;
    min-height: 250px;
  }
  
  .gallery-item {
    min-height: 250px;
  }
  
  .gallery-caption {
    font-size: 1rem;
    padding: 15px;
  }
}

.about::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  background: radial-gradient(circle at 10% 20%, var(--primary-light) 0%, transparent 50%);
  opacity: 0.1;
  pointer-events: none;
}

.gallery::after {
  content: '';
  position: absolute;
  bottom: 0;
  right: 0;
  width: 200px;
  height: 200px;
  background: radial-gradient(circle, var(--primary-color) 0%, transparent 70%);
  opacity: 0.1;
  pointer-events: none;
}

.no-photos {
  text-align: center;
  color: var(--text-light);
  font-size: 1.2rem;
  padding: 3rem 0;
  font-style: italic;
}